.App{
  min-height: 100vh;
  background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
}

.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar{
  width: 50%;
  border: none;
  padding: 10px;
  margin: 10px;
}

.search-button{
  background: lightseagreen;
  border-radius: 20px;
  padding: 10px 20px;
  color: white;
}

.recipes{
  display: flexbox;
  margin: 100px;
  justify-content: space-around;
  flex-wrap: wrap;
}

.head {
  background: lightseagreen;
  font-size: 20px;
  /* font-weight: bold; */
  color: white;
  display: flex;
  justify-content: center;
  padding: 10px 20px;
}

